import { SET_SCROLLER_PARAMS, SET_VIEWPORT_PARAMS } from '../actions';

const initialState = {
  scrollerWidth: 1, // Pixels
  viewportHeight: 1, // Pixels
  scrollerXPosition: 0, // Pixels
};

const presentation = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case SET_SCROLLER_PARAMS:
      return {
        ...state,
        ...payload,
      };
    case SET_VIEWPORT_PARAMS:
      return {
        ...state,
        viewportHeight: payload,
      };
    default:
      return state;
  }
};

export default presentation;
